import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { isEmbed, isSocialWall } from "../../../actions/api";
const ReviewHub = React.lazy(() => import('./ReviewHub'));


class UserComponents extends PureComponent {
  render() {
    const { modalPop, appData } = this.props
    return (<>
      {modalPop.onSiteIsShowPopUp && appData.wall && Object.keys(appData.wall.Wall).length > 0 && isEmbed ? <ReviewHub wall={appData.wall} wallId={appData.wall.Wall.id} feeds={appData.feeds} isFreeAdsStatus={appData.postData.isFreeAdsStatus} /> : ''}
    </>
    );
  }
}

const mapStateToProps = state => {
  return {
    modalPop: state.modalPop,
    appData: state.appData,
    loaderData: state.loaderData
  }
}

export default connect(mapStateToProps)(UserComponents);

